/* Add a specific body class when market is open */
body.mk-market-open {
  overscroll-behavior-y: none;
  overflow: hidden;
  touch-action: none;
}

.main-content {
  flex: 1;
  padding: 0;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  position: relative;
  height: 100vh;
  overflow: hidden;
}

/* Background Container */
.background-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.background-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Stats Header */
.stats-header {
  position: fixed;
  top: 18px;
  width: 500px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 28px;
  background: rgba(0, 0, 0, 0.5);
  pointer-events: auto;
  box-sizing: border-box;
  height: 60px;
  margin-top: 35px;
  backdrop-filter: blur(10px);
  border-radius: 12px;
}

.profile-pic-main {
  flex-shrink: 0;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: opacity 0.2s;
  margin-left: 0;
}

.profile-pic-main img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: #FF00F6 2px solid;
}

.stat-item-main-text {
  color: #FFF;
  font-family: "PP Neue Machina";
  font-size: 12px;
  font-style: italic;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  margin-top: 4px;
}

.stats-main {
  display: flex;
  gap: 16px;
  margin-left: auto;
}

.stat-item-main {
  display: flex;
  align-items: center;
  gap: 4px;
  min-width: fit-content;
  font-style: italic;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: opacity 0.2s;
}

.stat-item-main img {
  width: 20px;
  height: 20px;
}

/* Market Container */
.mk-market-container {
  display: flex;
  flex-direction: column;
  padding: 0;
  color: white;
  height: 100%;
  background-color: transparent;
  width: min(500px, 100%);
  margin: 0 auto;
  position: relative;
  overflow-y: auto;
  overscroll-behavior: none;
  -webkit-overflow-scrolling: touch;
  touch-action: pan-y;
  
  /* Hide scrollbars while maintaining functionality */
  scrollbar-width: none;  /* Firefox */
  -ms-overflow-style: none;  /* IE and Edge */
}

/* Hide WebKit scrollbars for the main container */
.mk-market-container::-webkit-scrollbar,
.mk-market-container::-webkit-scrollbar-thumb,
.mk-market-container::-webkit-scrollbar-track {
  width: 0 !important;
  height: 0 !important;
  background-color: transparent !important;
  display: none !important;
}

.mk-market-title {
  text-align: center;
  font-size: clamp(65px, 15vw, 90px);
  font-weight: 700;
  font-family: "PP Neue Machina";
  color: white;
  text-transform: uppercase;
  margin-top: 65px;
  padding: 0 28px;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  line-height: 1;
  display: flex;
  justify-content: center;
  height: 90px;
}

.mk-market-title span {
  width: 100%;
  display: block;
  text-align: center;
  white-space: nowrap;
  font-size: inherit;
  text-align-last: justify;
  -moz-text-align-last: justify;
  text-justify: distribute;
  letter-spacing: 0.1em;
  line-height: 120px;
  height: 120px;
}

.mk-fsl-connect-section {
  background: rgba(0, 255, 255, 0.1);
  border-radius: 12px;
  padding: 12px;
  margin: 16px 26px 0px;
  width: auto;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.mk-fsl-connect-section:hover {
  background: rgba(0, 255, 255, 0.2);
}

.mk-fsl-connect-content {
  display: flex;
  align-items: center;
  gap: 12px;
}

.mk-lock-icon {
  font-size: 24px;
  color: #00ffff;
  background: rgba(0, 255, 255, 0.2);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.mk-fsl-text {
  flex: 1;
}

.mk-connect-title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 4px;
  color: white;
}

.mk-connect-subtitle {
  font-size: 12px;
  opacity: 0.8;
  color: rgba(255, 255, 255, 0.8);
}

/* Market Content */
.mk-market-content {
  flex: 1;
  overflow-x: hidden;
  padding-top: 10px;
  padding-bottom: 91px;
  -webkit-overflow-scrolling: touch;
  position: relative;
  height: 100vh;
  margin-top: 0;
  overscroll-behavior: none;
  width: 100%;
  box-sizing: border-box;
}

.mk-scrollable-market-content {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
  min-height: 0;
  display: flex;
  flex-direction: column;
  height: fit-content;
  overscroll-behavior: none;
  -webkit-overflow-scrolling: touch;
  touch-action: pan-y;

  /* Add mask gradient for soft scroll boundary */
  -webkit-mask-image: linear-gradient(
    to bottom,
    transparent 0%,
    black 3%,
    black 97%,
    transparent 100%
  );
  mask-image: linear-gradient(
    to bottom,
    transparent 0%,
    black 3%,
    black 97%,
    transparent 100%
  );
}

/* Hide WebKit scrollbars */
.mk-scrollable-market-content::-webkit-scrollbar,
.mk-scrollable-market-content::-webkit-scrollbar-thumb,
.mk-scrollable-market-content::-webkit-scrollbar-track {
    width: 0 !important;
    height: 0 !important;
    background-color: transparent !important;
    display: none !important;
}

/* Ensure child elements also hide scrollbars */
.mk-scrollable-market-content * {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.mk-scrollable-market-content *::-webkit-scrollbar {
  display: none !important;
}

/* Tabs */
.mk-tabs {
  display: flex;
  gap: 16px;
  padding: 0px;
  flex-shrink: 0;
  width: 100%;
  margin-bottom: 16px;
}

.mk-tab {
  background: none;
  border: none;
  padding: 8px 0;
  cursor: pointer;
  opacity: 0.6;
  position: relative;
  width: 50%;

  color:#FFF;
  text-align: center;
  justify-content: center;
  align-items: center;

  /* APP/Body/Strong/Body-M-Strong */
  font-family: "PP Neue Machina";
  font-size: 14px;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 0px;
  transition: opacity 0.3s ease;
}

.mk-tab.active {
  opacity: 1;
}

.mk-tab.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 50%;
  transform: translateX(-50%);
  width: 75%;
  height: 2px;
  background: #0000FF;
}

/* Starlet Grid */
.mk-starlet-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}

/* Responsive grid adjustments */
@media screen and (max-width: 400px) {
  .mk-starlet-grid {
    grid-template-columns: repeat(2, 1fr); /* Keep 2 columns even on small screens */
    gap: 8px;
  }
}

/* Market Ticket Button */
.mk-market-ticket-button {
  position: relative;
  width: 100%;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  margin-bottom: 0px;
  transition: transform 0.2s ease;
}

.mk-market-ticket-button:hover {
  transform: translateY(-2px);
}

.mk-market-ticket-button-image-container {
  position: relative;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid #0000FF;
  display: flex;
  flex-direction: column;
}

.mk-market-ticket-content {
  display: flex;
  align-items: center;
  padding: 8px;
  background: rgba(0, 0, 0, 0.5);
  gap: 8px;
  border-bottom: 1px solid #0000FF;
  height: 55px;
  backdrop-filter: blur(5px);
}

.mk-market-ticket-button:hover .mk-market-ticket-content {
  background: rgba(0, 0, 255, 0.1);
}

.mk-market-ticket-button.sold-out {
  cursor: not-allowed;
  opacity: 0.7;
}

.mk-market-ticket-button.sold-out:hover {
  transform: none;
}

.mk-market-ticket-button.sold-out .mk-market-ticket-price {
  background: #808080;
}

.mk-market-ticket-button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

.mk-market-ticket-button:disabled:hover {
  transform: none;
}

.mk-market-ticket-button:disabled .mk-market-ticket-content {
  background: rgba(0, 0, 0, 0.5) !important;
}

.mk-market-ticket-icon {
  width: 35%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background: transparent;
  position: relative;
}

.mk-market-ticket-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: relative;
  z-index: 1;
}

.mk-market-ticket-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0px;
  justify-content: center;
  align-items: flex-start;
  width: 65%;
  padding-left: 4px;
}

.mk-market-ticket-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  gap: 0px;
  width: fit-content;
}

.mk-market-ticket-amount {
  font-family: "PP Neue Machina";
  font-size: 30px;
  font-weight: 700;
  color: white;
  line-height: 1;
}

.mk-market-ticket-label {
  font-family: "PP Neue Machina";
  font-size: 14px;
  color: white;
  opacity: 1;
  line-height: 1;
  text-transform: uppercase;
  margin-top: -2px;
  min-width: 70px;
  text-align: left;
  font-weight: 550;
  letter-spacing: 0.02em;
}

.mk-market-ticket-bonus {
  padding: 0.5px 4px;
  background: #00FF00;
  color: black;
  font-family: "PP Neue Machina";
  font-size: 10px;
  font-weight: bold;
  min-width: 70px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: left;
  white-space: nowrap;
  margin-top: 2px;
}

.mk-market-ticket-price {
  height: 20px;
  background: #0000FF;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "PP Neue Machina";
  font-size: 12px;
  font-weight: 500;
}

/* Starlet Packages Placeholder */
.mk-starlet-packages-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-height: 300px;
  width: 100%;
}

.mk-placeholder-content {
  text-align: center;
  padding: 40px 20px;
}

.mk-placeholder-text {
  color: #FFF;
  font-family: "PP Neue Machina";
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 8px;
}

.mk-placeholder-subtext {
  color: rgba(255, 255, 255, 0.6);
  font-family: "PP Neue Machina";
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
}

/* Payment Content (Keep existing styles) */
.payment-content {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 0 20px;
}

.payment-info {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  padding: 30px;
  backdrop-filter: blur(10px);
}

.section-title {
  font-family: "PP Neue Machina", sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: white;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.payment-description {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  margin-bottom: 30px;
}

.payment-steps {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.step {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.step-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea, #764ba2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "PP Neue Machina", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: white;
  flex-shrink: 0;
}

.step-content h3 {
  font-family: "PP Neue Machina", sans-serif;
  font-size: 16px;
  font-weight: 700;
  color: white;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.step-content p {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.5;
}

/* Responsive Design */
@media screen and (max-width: 500px) {
  .mk-market-container {
    width: 100%;
    overscroll-behavior-y: none;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    touch-action: pan-y;
  }
  
  .stats-header {
    width: 100%;
    margin: 0 16px;
    width: calc(100% - 32px);
  }

  .mk-market-title {
    font-size: 28px;
    margin-top: 80px;
  }
  
  .payment-info {
    padding: 20px;
  }
  
  .step {
    gap: 15px;
  }
  
  .step-number {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }
}

/* Ensure all children in market container have consistent overscroll behavior */
.mk-market-container * {
  overscroll-behavior: inherit;
} 